//Import font Liquide

@font-face {
    font-family: 'liquide';
    src: url('../../fonts/Liquide/liquide_-_free_typeface-webfont.woff2') format('woff2'),
         url('../../fonts/Liquide/liquide_-_free_typeface-webfont.woff') format('woff');
    font-weight: normal;
    font-style: normal;
}



