.menu_de_navigation {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-top: 27px;
  padding-left: 32px;
  padding-right: 32px;
}
.menu_de_navigation__partie_gauche {
  display: flex;
  align-items: center;
  gap: 34px;
}
.menu_de_navigation__partie_gauche--sombre {
  color: #F0F2FE;
}
.menu_de_navigation__partie_gauche--clair {
  color: #1A0045;
}
.menu_de_navigation__partie_gauche__logo {
  transition: transform 0.2s ease-in-out;
}
.menu_de_navigation__partie_gauche__logo:hover {
  transform: scale(1.15);
}
.menu_de_navigation__partie_gauche span {
  font-family: "Chillax-Medium";
  font-style: normal;
  font-weight: 500;
  font-size: 17px;
  line-height: 24px;
}
.menu_de_navigation__partie_droite {
  display: flex;
  align-items: center;
  gap: 34px;
}

@media screen and (max-width: 460px) {
  .menu_de_navigation__partie_gauche span {
    display: none;
  }
}

